.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px; /* Adjust the gap as necessary */
  height: 100vh;
}

.oval {
  border: 2px solid;
  border-radius: 50px; 
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1em;
  position: relative; 
}
.inner-entity {
  width: 200px; 
  height: 200px; 
  border-radius: 10px; 
  border: 2px solid;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  
  
}
.western {
  border-color: blue;
  
  height: 900px; 
  width: 400px; 
  flex: 0 0 400px; 
}

.label, .inner-circle {
  position: absolute;
  top: -25px; 
  padding: 1px;
  background-color: white;
  font-size: 28px; 
}

.western .label {
  left: 50%;
  transform: translateX(-50%);
}

.eastern {
  border-color: red;
  height: 900px; 
  width: 400px; 
  flex: 0 0 400px;
}

.southern {
  border-color: orange;
  border-radius: 50px;
  width: 65%; 
  height: 300px; 
}

.diasporic {
  border-color: purple;
  width: 250px; 
  height: 250px; 
}


.western .inner-entity {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.western .america {
  top: 20%; 
  border-color: black;
}

.western .europe {
  top: 50%;
  border-color: black;
}

.eastern .inner-entity {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.eastern .ussr {
  top: 10%; 
  border-color: black;
}

.eastern .china {
  top: 40%; 
  border-color: black;
}

.eastern .japan {
  top: 70%; 
  border-color: black;
}

.inner-row {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; 
}

.southern .inner-entity {
  position: relative;
  width: auto;
  padding: 5px 30px; 
  margin: 0 30px; 
  border-radius: 10px; 
  border: 2px solid;
  flex-grow: 1;
}

.southern .latin-america {
  border-color: black;
}

.southern .india {
  border-color: black;
}

.southern .africa {
  border-color: black;
}

.concepts-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%; 
}

.concept {
  cursor: pointer;
  padding: 5px 10px;
  margin: 2px 0; 
  transition: color 0.3s ease;
  color: #000; 
  font-size: 20px;

  text-align: center; 
}

.concept.active {
  color: #FDE295;  
  font-weight: bold; 
}

@keyframes glow {
  from {
    box-shadow: 0 0 5px #EAD7D3;
  }  
  to {
    box-shadow: 0 0 20px #EAD7D3;
  }
}


.text-area {
  padding: 10px;
  border: 2px solid brown;
  border-radius: 5px;
  width: 40%; 
  min-height: 150px; 
  overflow-y: auto; 
  margin-top: 20px;
}

.text-area > div {
  margin-bottom: 10px;
  padding: 5px;
  font-size: 20px;
}

.label.text-area-label {
  position: relative;
  top: -65px; 
  left: 50%;
  transform: translateX(-50%);

}

